import React from "react";
import {BigNumber, ethers} from "ethers";
import {parseUnits} from "@ethersproject/units";

export const handleAmountInput = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, setter: (value: string) => void, decimals: number, maxWei?: BigNumber) => {
    const maxAmountWei = maxWei ?? ethers.constants.MaxUint256
    const amountString = e.target.value
    if (amountString === "") setter("")
    let amountAsWei: BigNumber
    try {
        // try to parse the string into a BigNumber
        amountAsWei = parseUnits(amountString, decimals)
    } catch (e) {
        // if this fails return without calling the setter
        return
    }
    if (amountAsWei.lte(maxAmountWei) && !amountAsWei.isNegative()) {
        setter(amountString)
    }
}
