import {Button} from "@mui/material"
import {useEthers} from "@usedapp/core"
import Box from '@mui/material/Box'
import {openAddressInEtherscan} from "../utils/etherscan"

export const Header = () => {
    const {active, account, chainId, activateBrowserWallet, deactivate} = useEthers()

    const accountClickHandler = () => {
        openAddressInEtherscan(chainId, account)
    }

    return (
        <Box sx={{padding: 4, display: "flex", justifyContent: "flex-end", gap: 1}}>
            {(active && account) ? (
                <>
                    <Button color="primary" variant="contained" onClick={accountClickHandler}>
                        {`${account?.slice(0, 6)}...${account?.slice(-4)}`}
                    </Button>
                    <Button variant="contained" onClick={() => deactivate()}>
                        Disconnect
                    </Button>
                </>
            ) : (
                <Button
                    color="primary"
                    variant="contained"
                    onClick={() => activateBrowserWallet()}
                >
                    Connect
                </Button>
            )}
        </Box>
    )
}
