import React, {useEffect, useState} from "react"
import {Snackbar} from "@mui/material";
import Alert from "@mui/material/Alert";
import {SnackbarCloseReason} from "@mui/material/Snackbar/Snackbar";
import {config} from "../../useDappConfig";

interface TxStatusToastProps {
    chainId: number | undefined
    error: Error | undefined
    deactivate: () => void
}

export const NetworkErrorToast = ({chainId, error, deactivate}: TxStatusToastProps) => {

    const [openSnackbar, setOpenSnackbar] = useState(false)

    useEffect(() => {
        if(config.networks!!.filter(n=>n.chainId===chainId).length===0) {
            // the chainId of the wallet is not configured in hardhat.config.ts
            !openSnackbar && setOpenSnackbar(true)
        }
        else if (error && error.name === "ChainIdError") {
            // the chainId is not configured in index.tsx
            !openSnackbar && setOpenSnackbar(true)
        } else {
            openSnackbar && setOpenSnackbar(false)
        }
    }, [chainId, error, openSnackbar])

    const handleOnClose = (event: React.SyntheticEvent<any> | Event, reason?: SnackbarCloseReason) => {
        if (reason === "timeout" || reason === "clickaway") {
            // Do not hide the error in case of these reasons.
            // Note that if the close button is clicked the reason is an empty string.
            return
        }
        deactivate()
        openSnackbar && setOpenSnackbar(false)
    }

    return <Snackbar
        open={openSnackbar}
        autoHideDuration={10000}
        onClose={handleOnClose}
        anchorOrigin={{vertical: "bottom", horizontal: "center"}}
    >
        <Alert onClose={handleOnClose} severity="warning">
            Please connect to one of the supported networks: {
            config.networks!!.map(c => c.chainName).join(', ')}
        </Alert>
    </Snackbar>
}
