import React, {useState} from "react"
import {useCall} from "@usedapp/core"
import {Box, Tab} from "@mui/material";
import {TabContext, TabList, TabPanel} from "@mui/lab"
import {Contract} from "@ethersproject/contracts";
import {CompensatePanel} from "./CompensatePanel";
import {TrusteePanel} from "./TrusteePanel";
import {FeesPanel} from "./FeesPanel";
import {TrusteesPanel} from "./TrusteesPanel";

interface TablistProps {
    contract: Contract
    account: string
}

export const Tablist = ({contract, account}: TablistProps) => {
    const [selectedTabIndex, setSelectedTabIndex] = useState<number>(0)

    const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
        setSelectedTabIndex(parseInt(newValue))
    }

    const isTrustee = useCall({contract, method: 'isTrustee', args: [account]});
    const showTrusteeTab = isTrustee !== undefined && isTrustee.value[0] === true

    const owner = useCall({contract, method: 'owner', args: []});
    const showAdminTabs = owner !== undefined && owner.value[0] === account;

    let tabs = []
    let tabPanels = []
    tabs.push(<Tab
        label="Compensate"
        value="0"
        key={0}
    />)
    tabPanels.push(<TabPanel value={"0"} key={0}>
        <Box sx={{display: "flex", flexDirection: "column", alignItems: "center", gap: 4}}>
            <CompensatePanel contract={contract} account={account}/>
        </Box>
    </TabPanel>)

    if (showTrusteeTab) {
        tabs.push(<Tab
                label="Trustee"
                value="1"
                key={1}
            />
        )
        tabPanels.push(<TabPanel value={"1"} key={1}>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "center", gap: 4}}>
                <TrusteePanel contract={contract} account={account}/>
            </Box>
        </TabPanel>)
    }

    if (showAdminTabs) {
        tabs.push(<Tab
            label="Fees"
            value="2"
            key={2}
        />)
        tabPanels.push(<TabPanel value={"2"} key={2}>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "center", gap: 4}}>
                <FeesPanel/>
            </Box>
        </TabPanel>)
        tabs.push(<Tab
            label="Trustees"
            value="3"
            key={3}
        />)
        tabPanels.push(<TabPanel value={"3"} key={3}>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "center", gap: 4}}>
                <TrusteesPanel/>
            </Box>
        </TabPanel>)
    }

    return (
        <TabContext value={selectedTabIndex.toString()}>
            <TabList onChange={handleChange} aria-label="tabs" TabIndicatorProps={{sx: {transition: "none"}}}>
                {tabs}
            </TabList>
            {tabPanels}
        </TabContext>
    )
}
