import React, {useEffect, useState} from "react"
import {formatUnits} from "@ethersproject/units"
import {Button, CircularProgress, InputAdornment, TextField, Typography} from "@mui/material";
import {BigNumber, utils} from "ethers"
import Box from '@mui/material/Box';
import {handleAmountInput} from "../../utils/handleAmountInput";
import {useFees} from "../../hooks/useFees";
import {TxStatusToast} from "../toast/TxStatusToast";
import {isTxBusy} from "../../utils/transactionUiState";

export const FeesPanel = () => {
    const {
        mintFee, mintFeeSend, mintFeeState, mintFeeResetState,
        burnFee, burnFeeSend, burnFeeState, burnFeeResetState,
        feeDecimals
    } = useFees()
    const [mintFeeInput, setMintFeeInput] = useState<number | string | Array<number | string>>(0)
    const [mintFeeError, setMintFeeError] = useState(false)
    const [mintFeeErrorHelperText, setMintFeeErrorHelperText] = useState("")
    const [burnFeeInput, setBurnFeeInput] = useState<number | string | Array<number | string>>(0)
    const [burnFeeError, setBurnFeeError] = useState(false)
    const [burnFeeErrorHelperText, setBurnFeeErrorHelperText] = useState("")

    const decimals = feeDecimals ?? 8

    useEffect(() => {
        const adjustMintFeeError = (text: string | undefined) => {
            if (text) {
                !mintFeeError && setMintFeeError(true)
                mintFeeErrorHelperText !== text && setMintFeeErrorHelperText(text)
            } else {
                mintFeeError && setMintFeeError(false)
                mintFeeErrorHelperText && setMintFeeErrorHelperText("")
            }
        }

        const mintFeeString = mintFeeInput.toString()
        if (mintFeeString === "") {
            adjustMintFeeError("field must not be empty")
            return
        }
        adjustMintFeeError(undefined)
    }, [mintFeeInput, mintFeeError, mintFeeErrorHelperText])

    useEffect(() => {
        const adjustBurnFeeError = (text: string | undefined) => {
            if (text) {
                !burnFeeError && setBurnFeeError(true)
                burnFeeErrorHelperText !== text && setBurnFeeErrorHelperText(text)
            } else {
                burnFeeError && setBurnFeeError(false)
                burnFeeErrorHelperText && setBurnFeeErrorHelperText("")
            }
        }

        const burnFeeString = burnFeeInput.toString()
        if (burnFeeString === "") {
            adjustBurnFeeError("field must not be empty")
            return
        }
        adjustBurnFeeError(undefined)
    }, [burnFeeInput, burnFeeError, burnFeeErrorHelperText])

    const handleMintFeeSubmit = () => {
        const amountAsWei = utils.parseUnits(mintFeeInput.toString(), decimals - 2)
        return mintFeeSend(amountAsWei.toString())
    }

    const handleBurnFeeSubmit = () => {
        const amountAsWei = utils.parseUnits(burnFeeInput.toString(), decimals - 2)
        return burnFeeSend(amountAsWei.toString())
    }

    const isBusy = isTxBusy(mintFeeState.status) || isTxBusy(burnFeeState.status)
    const formattedMintFeePercent = mintFee ? formatUnits(mintFee, decimals - 2) : "0"
    const formattedBurnFeePercent = burnFee ? formatUnits(burnFee, decimals - 2) : "0"

    const handleFeeInput = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, setter: (value: string) => void) => {
        const percentageDecimals = decimals - 2
        const hundredPercentWei = BigNumber.from(10).pow(decimals)
        handleAmountInput(e, setter, percentageDecimals, hundredPercentWei)
    }

    return (
        <>
            <Box sx={{display: "grid", gridTemplateColumns: "repeat(3, 1fr)", gap: 2}}>
                <Box sx={{display: "flex", alignItems: "center"}}>
                    <Typography>
                        Current mint fee: <b>{formattedMintFeePercent}%</b>
                    </Typography>
                </Box>
                <TextField value={mintFeeInput}
                           onChange={e => handleFeeInput(e, setMintFeeInput)}
                           label="new mint fee"
                           InputProps={{endAdornment: <InputAdornment position="end">%</InputAdornment>}}
                           disabled={isBusy}
                           error={mintFeeError}
                           helperText={mintFeeErrorHelperText}
                />
                <Button
                    color="primary"
                    variant="contained"
                    size="large"
                    onClick={handleMintFeeSubmit}
                    disabled={isBusy || mintFeeError}
                >
                    {isBusy ? <CircularProgress size={26}/> : "Update"}
                </Button>

                <Box sx={{display: "flex", alignItems: "center"}}>
                    <Typography>
                        Current burn fee: <b>{formattedBurnFeePercent}%</b>
                    </Typography>
                </Box>
                <TextField value={burnFeeInput}
                           onChange={e => handleFeeInput(e, setBurnFeeInput)}
                           label="new burn fee"
                           InputProps={{endAdornment: <InputAdornment position="end">%</InputAdornment>}}
                           disabled={isBusy}
                           error={burnFeeError}
                           helperText={burnFeeErrorHelperText}
                />
                <Button
                    color="primary"
                    variant="contained"
                    size="large"
                    onClick={handleBurnFeeSubmit}
                    disabled={isBusy || burnFeeError}
                >
                    {isBusy ? <CircularProgress size={26}/> : "Update"}
                </Button>
            </Box>
            <TxStatusToast successMessage="Mint fee successfully updated!"
                           state={mintFeeState}
                           resetState={mintFeeResetState}/>
            <TxStatusToast successMessage="Burn fee successfully updated!"
                           state={burnFeeState}
                           resetState={burnFeeResetState}/>
        </>
    )
}
