import React, {useEffect, useState} from "react"
import {useToken, useTokenBalance} from "@usedapp/core"
import {formatUnits} from "@ethersproject/units"
import {Button, CircularProgress, InputAdornment, TextField} from "@mui/material";
import {utils} from "ethers"
import {Contract} from "@ethersproject/contracts";
import Box from '@mui/material/Box';
import {useBurnTokens} from "../../hooks/useBurnTokens";
import {handleAmountInput} from "../../utils/handleAmountInput";
import {TxStatusToast} from "../toast/TxStatusToast";
import {isTxBusy} from "../../utils/transactionUiState";

interface CompensateProps {
    contract: Contract
    account: string
}

export const CompensatePanel = ({contract, account}: CompensateProps) => {
    const bocaBalance = useTokenBalance(contract.address, account)
    const bocaInfo = useToken(contract.address)
    const {send: burnTokensSend, state: burnTokensState, resetState: burnTokensResetState} = useBurnTokens()
    const [burnAmount, setBurnAmount] = useState<number | string | Array<number | string>>(0)
    const [burnAmountError, setBurnAmountError] = useState(true)
    const [burnAmountHelperText, setBurnAmountHelperText] = useState("")

    const decimals = bocaInfo?.decimals ?? 18

    useEffect(() => {
        const adjustBurnAmountError = (text: string | undefined) => {
            if (text) {
                !burnAmountError && setBurnAmountError(true)
                burnAmountHelperText !== text && setBurnAmountHelperText(text)
            } else {
                burnAmountError && setBurnAmountError(false)
                burnAmountHelperText && setBurnAmountHelperText("")
            }
        }

        const burnAmountString = burnAmount.toString()
        if (burnAmountString === "") {
            adjustBurnAmountError("field must not be empty")
            return
        }
        const burnAmountAsWei = utils.parseUnits(burnAmount.toString(), decimals)
        if (burnAmountAsWei.isZero()) {
            adjustBurnAmountError("value must not be 0")
            return
        }
        if (bocaBalance?.lt(burnAmountAsWei)) {
            adjustBurnAmountError("insufficient funds")
            return
        }
        adjustBurnAmountError(undefined)
    }, [bocaBalance, burnAmount, burnAmountError, burnAmountHelperText, decimals])


    const handleBurnSubmit = () => {
        const amountAsWei = utils.parseUnits(burnAmount.toString(), decimals)
        return burnTokensSend(amountAsWei.toString())
    }

    const isBusy = isTxBusy(burnTokensState.status)
    const formattedBocaBalance = bocaBalance ? formatUnits(bocaBalance, decimals) : "0"

    return (
        <>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "left", gap: 2, width: "100%"}}>
                <span>Each Bound Carbon (BOCA) token represents 1t of CO<sub>2</sub>.</span>
                <span>Your current token balance is: <b>{formattedBocaBalance} BOCA</b></span>
                <span>How many tons of CO<sub>2</sub> do you want to compensate by burning BOCA tokens?</span>
                <TextField value={burnAmount}
                           onChange={e => handleAmountInput(e, setBurnAmount, decimals)}
                           label="tokens to burn"
                           InputProps={{endAdornment: <InputAdornment position="end">BOCA</InputAdornment>}}
                           disabled={isBusy}
                           error={burnAmountError}
                           helperText={burnAmountHelperText}
                />
                <Button
                    color="primary"
                    variant="contained"
                    size="large"
                    onClick={handleBurnSubmit}
                    disabled={isBusy || burnAmountError}
                >
                    {isBusy ? <CircularProgress size={26}/> : "Compensate"}
                </Button>
            </Box>
            <TxStatusToast successMessage="BOCA tokens burned successfully!"
                           state={burnTokensState}
                           resetState={burnTokensResetState}/>
        </>
    )
}
