import React, {useEffect, useState} from "react"
import {Button, CircularProgress, TextField, Typography} from "@mui/material";
import Box from '@mui/material/Box';
import {useTrustees} from "../../hooks/useTrustees";
import {TxStatusToast} from "../toast/TxStatusToast";
import {isTxBusy} from "../../utils/transactionUiState";

export const TrusteesPanel = () => {
    const {
        trustees,
        addTrusteeSend, addTrusteeState, addTrusteeResetState,
        removeTrusteeSend, removeTrusteeState, removeTrusteeResetState
    } = useTrustees()
    const [trusteeInput, setTrusteeInput] = useState<number | string | Array<number | string>>("")
    const [trusteeInputError, setTrusteeInputError] = useState(true)
    const [trusteeInputErrorHelperText, setTrusteeInputErrorHelperText] = useState("")

    useEffect(() => {
        const adjustTrusteeInputError = (text: string | undefined) => {
            if (text) {
                !trusteeInputError && setTrusteeInputError(true)
                trusteeInputErrorHelperText !== text && setTrusteeInputErrorHelperText(text)
            } else {
                trusteeInputError && setTrusteeInputError(false)
                trusteeInputErrorHelperText && setTrusteeInputErrorHelperText("")
            }
        }

        const trusteeInputString = trusteeInput.toString()
        if (trusteeInputString === "") {
            adjustTrusteeInputError("field must not be empty")
            return
        }
        const regexp = /^0x[a-fA-F0-9]{40}$/
        if (!regexp.test(trusteeInputString)) {
            adjustTrusteeInputError("invalid ethereum address")
            return
        }
        if (trustees.map(t => t.toLowerCase()).includes(trusteeInputString.toLowerCase())) {
            adjustTrusteeInputError("trustee already exists")
            return
        }
        adjustTrusteeInputError(undefined)
    }, [trusteeInput, trusteeInputError, trusteeInputErrorHelperText, trustees])

    const handleTrusteeInputSubmit = () => {
        const trusteeAddress = trusteeInput.toString()
        return addTrusteeSend(trusteeAddress)
    }

    const handleRemoveTrusteeSubmit = (trusteeAddress: string) => {
        return removeTrusteeSend(trusteeAddress)
    }

    const isBusy = isTxBusy(addTrusteeState.status) || isTxBusy(removeTrusteeState.status)

    const handleTrusteeInput = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>, setter: (value: string) => void) => {
        const regexp = /^(?:0?|0x[a-fA-F0-9]{0,40})$/
        const addressInput = e.target.value
        regexp.test(addressInput) && setter(addressInput)
    }

    return (
        <>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "left", gap: 2, width: "100%"}}>
                <Typography variant="h5" component="h4">Add Trustee</Typography>
                <Box sx={{display: "grid", gridTemplateColumns: "2fr 1fr", gap: 2}}>
                    <TextField value={trusteeInput}
                               onChange={e => handleTrusteeInput(e, setTrusteeInput)}
                               label="new trustee address"
                               disabled={isBusy}
                               error={trusteeInputError}
                               helperText={trusteeInputErrorHelperText}
                               spellCheck="false"
                               inputProps={{style: {fontFamily: "monospace"}}}
                    />
                    <Button
                        color="primary"
                        variant="contained"
                        size="large"
                        onClick={handleTrusteeInputSubmit}
                        disabled={isBusy || trusteeInputError}
                    >
                        {isBusy ? <CircularProgress size={26}/> : "Add trustee"}
                    </Button>
                </Box>

                <Typography variant="h5" component="h4" sx={{pt: 2}}>Registered Trustees</Typography>
                <Box sx={{display: "grid", gridTemplateColumns: "2fr 1fr", gap: 2}}>
                    {trustees.reduce((accu, addr) => accu.concat(
                        <Box key={`box_${addr}`} sx={{display: "flex", alignItems: "center"}}>
                            <Typography fontFamily={"monospace"}>
                                <b>{addr}</b>
                            </Typography>
                        </Box>
                    ).concat(
                        <Button
                            key={`button_${addr}`}
                            color="primary"
                            variant="contained"
                            size="large"
                            onClick={() => handleRemoveTrusteeSubmit(addr)}
                            disabled={isBusy}
                        >
                            {isBusy ? <CircularProgress size={26}/> : "Remove trustee"}
                        </Button>
                    ), Array<React.JSX.Element>())}
                </Box>
            </Box>
            <TxStatusToast successMessage="Trustees successfully added!"
                           state={addTrusteeState}
                           resetState={addTrusteeResetState}/>
            <TxStatusToast successMessage="Trustees successfully removed!"
                           state={removeTrusteeState}
                           resetState={removeTrusteeResetState}/>
        </>
    )
}
