import {useCall, useContractFunction, useEthers} from "@usedapp/core"
import {createBocaContract} from "../utils/createBocaContract";

export const useTrustees = () => {
    const {chainId} = useEthers()
    const bocaContract = createBocaContract(chainId)

    const {value: trusteesValue} = useCall({
        contract: bocaContract,
        method: "getTrustees",
        args: [],
    }) ?? {}
    const trustees: string[] = trusteesValue ? trusteesValue[0] : [];

    const {send: addTrusteeSend, state: addTrusteeState, events: addTrusteeEvents, resetState: addTrusteeResetState} = useContractFunction(bocaContract, "addTrustee", {
        transactionName: "Add trustee",
    })

    const {send: removeTrusteeSend, state: removeTrusteeState, events: removeTrusteeEvents, resetState: removeTrusteeResetState} = useContractFunction(bocaContract, "removeTrustee", {
        transactionName: "Remove trustee",
    })

    return {trustees, addTrusteeSend, addTrusteeState, addTrusteeEvents, addTrusteeResetState, removeTrusteeSend, removeTrusteeState, removeTrusteeEvents, removeTrusteeResetState}
}
