import React, {ReactNode, useEffect, useState} from "react"
import {Link, Snackbar} from "@mui/material";
import Alert from "@mui/material/Alert";
import {AlertColor} from "@mui/material/Alert/Alert";
import {TransactionStatus, useEthers} from "@usedapp/core";
import {isValidChain, openTransactionInEtherscan} from "../../utils/etherscan";

interface TxStatusToastProps {
    successMessage: ReactNode
    state: TransactionStatus
    resetState: () => void
}

const DEFAULT_HIDE_DURATION = 10_000

export const TxStatusToast = ({successMessage, state, resetState}: TxStatusToastProps) => {
    const {chainId} = useEthers()
    const [message, setMessage] = useState<ReactNode>()
    const [hideDuration, setHideDuration] = useState<number | null>(DEFAULT_HIDE_DURATION)
    const [severity, setSeverity] = useState<AlertColor>()

    useEffect(() => {
        const getTransactionLink = (chainId: number | undefined, txHash: string | undefined, message: ReactNode) => {
            return txHash && isValidChain(chainId) ?
                <Link
                    onClick={() => openTransactionInEtherscan(chainId, txHash)}>
                    {message}
                </Link> : message
        }

        const getSuccessMessageWithTx = (successMessage: ReactNode, state: TransactionStatus): ReactNode => {
            const txHash = state.transaction?.hash
            return getTransactionLink(chainId, txHash, successMessage)
        }

        const getTransactionMessage = (state: TransactionStatus): ReactNode => {
            const txHash = state.transaction?.hash
            return <>
                {getTransactionLink(chainId, txHash, "Transaction")} in state: {state.status}
            </>
        }

        switch (state.status) {
            case "Success":
                const successMessageWithTx = getSuccessMessageWithTx(successMessage, state)
                message !== successMessageWithTx && setMessage(successMessageWithTx)
                severity !== "success" && setSeverity("success")
                hideDuration !== DEFAULT_HIDE_DURATION && setHideDuration(DEFAULT_HIDE_DURATION)
                resetState()
                break
            case "PendingSignature":
            case "Mining":
            case "CollectingSignaturePool":
                const transactionMessage = getTransactionMessage(state)
                message !== transactionMessage && setMessage(transactionMessage)
                hideDuration !== null && setHideDuration(null)
                severity !== "info" && setSeverity("info")
                break
            case "Fail":
                const failMessageMessage = getTransactionMessage(state)
                message !== failMessageMessage && setMessage(failMessageMessage)
                hideDuration !== null && setHideDuration(null)
                severity !== "error" && setSeverity("error")
                resetState()
                break
            case "Exception":
                const errorMessage = state.errorMessage
                message !== errorMessage && setMessage(errorMessage)
                hideDuration !== null && setHideDuration(null)
                severity !== "error" && setSeverity("error")
                resetState()
                break
        }
    }, [successMessage, state, resetState, chainId, message, hideDuration, severity])

    const handleOnClose = () => {
        message && setMessage(undefined)
    }

    return <Snackbar
        open={message !== undefined}
        autoHideDuration={hideDuration}
        onClose={handleOnClose}
        anchorOrigin={{vertical: "bottom", horizontal: "center"}}
    >
        <Alert onClose={handleOnClose} severity={severity}>{message}</Alert>
    </Snackbar>
}
