import {constants, Contract} from "ethers";
import {ChainId} from "@usedapp/core";
import localhostBocaToken from "../deployments/localhost/BocaToken.json";
import sepoliaBocaToken from "../deployments/sepolia/BocaToken.json";

export const createBocaContract = (chainId: number | undefined): Contract => {
    let bocaTokenJson: Record<string, any> | undefined
    switch (chainId) {
        case ChainId.Localhost:
            bocaTokenJson = localhostBocaToken
            break
        case ChainId.Sepolia:
            bocaTokenJson = sepoliaBocaToken
            break
        default:
            bocaTokenJson = undefined
    }
    const address = bocaTokenJson ? bocaTokenJson["address"] : constants.AddressZero
    const abi = bocaTokenJson ? bocaTokenJson["abi"] : localhostBocaToken["abi"]
    return new Contract(address, abi)
}
