import React, {useEffect, useState} from "react"
import {useEthers} from "@usedapp/core"
import {Contract} from "ethers"
import {Box, Typography} from "@mui/material";
import {createBocaContract} from "../utils/createBocaContract";
import {NetworkErrorToast} from "./toast/NetworkErrorToast";
import {Tablist} from "./tabpanel/Tablist";
import {ConnectionRequiredMsg} from "./message/ConnectionRequiredMsg";

export const Main = () => {
    const {chainId, error, deactivate, account} = useEthers()
    const [bocaContract, setBocaContract] = useState<Contract>()

    useEffect(() => {
        if (chainId) {
            const bocaContract = createBocaContract(chainId)
            setBocaContract(bocaContract)
        } else {
            setBocaContract(undefined)
        }
    }, [chainId])

    const isConnected = bocaContract !== undefined && account !== undefined
    return (
        <>
            <Typography
                variant="h1"
                color={"white"}
                textAlign={"center"}
                padding={4}
            >
                Global Cooling
            </Typography>
            <Box>
                <Box sx={{backgroundColor: "white", borderRadius: "25px"}}>
                    <div>
                        {isConnected ? (
                            <Tablist contract={bocaContract} account={account}/>
                        ) : (
                            <ConnectionRequiredMsg/>
                        )}
                    </div>
                </Box>
            </Box>
            <NetworkErrorToast chainId={chainId} error={error} deactivate={deactivate}/>
        </>
    )
}
