import React, {useEffect, useState} from "react"
import {useToken, useTokenBalance} from "@usedapp/core"
import {formatUnits} from "@ethersproject/units"
import {Button, CircularProgress, InputAdornment, TextField} from "@mui/material";
import {utils} from "ethers"
import {Contract} from "@ethersproject/contracts";
import Box from '@mui/material/Box';
import {useMintTokens} from "../../hooks/useMintTokens";
import {handleAmountInput} from "../../utils/handleAmountInput";
import {TxStatusToast} from "../toast/TxStatusToast";
import {isTxBusy} from "../../utils/transactionUiState";

interface TrusteeProps {
    contract: Contract
    account: string
}

export const TrusteePanel = ({contract, account}: TrusteeProps) => {
    const bocaBalance = useTokenBalance(contract.address, account)
    const bocaInfo = useToken(contract.address)
    const {send: mintTokensSend, state: mintTokensState, resetState: mintTokensResetState} = useMintTokens()
    const [mintAmount, setMintAmount] = useState<number | string | Array<number | string>>(0)
    const [mintAmountError, setMintAmountError] = useState(true)
    const [mintAmountHelperText, setMintAmountHelperText] = useState("")

    const decimals = bocaInfo?.decimals ?? 18

    useEffect(() => {
        const adjustMintAmountError = (text: string | undefined) => {
            if (text) {
                !mintAmountError && setMintAmountError(true)
                mintAmountHelperText !== text && setMintAmountHelperText(text)
            } else {
                mintAmountError && setMintAmountError(false)
                mintAmountHelperText && setMintAmountHelperText("")
            }
        }

        const mintAmountString = mintAmount.toString()
        if (mintAmountString === "") {
            adjustMintAmountError("field must not be empty")
            return
        }
        const mintAmountAsWei = utils.parseUnits(mintAmount.toString(), decimals)
        if (mintAmountAsWei.isZero()) {
            adjustMintAmountError("value must not be 0")
            return
        }
        adjustMintAmountError(undefined)
    }, [mintAmount, mintAmountError, mintAmountHelperText, decimals])

    const handleMintSubmit = () => {
        const amountAsWei = utils.parseUnits(mintAmount.toString(), decimals)
        return mintTokensSend(amountAsWei.toString())
    }

    const isBusy = isTxBusy(mintTokensState.status)
    const formattedBocaBalance = bocaBalance ? formatUnits(bocaBalance, decimals) : "0"

    return (
        <>
            <Box sx={{display: "flex", flexDirection: "column", alignItems: "left", gap: 2, width: "100%"}}>
                <span>As a trustee you can generate Bound Carbon (BOCA) tokens and sell them on an exchange.
                    The revenue shall be used to purchase CO<sub>2</sub> certificates.
                    This process is called <i>minting</i>.
                    One BOCA is supposed to represent the market value of 1t of bound CO<sub>2</sub>.
                    Therefore you are only allowed to mint BOCA tokens if you are able to generate a higher revenue
                    by selling them than the expenses needed for purchasing CO<sub>2</sub> certificates
                    that represent the same weight of CO<sub>2</sub>.
                </span>
                <span>Your current token balance is: <b>{formattedBocaBalance} BOCA</b></span>
                <span>How many BOCA tokens do you want to mint?</span>
                <TextField value={mintAmount}
                           onChange={e => handleAmountInput(e, setMintAmount, decimals)}
                           label="tokens to mint"
                           InputProps={{endAdornment: <InputAdornment position="end">BOCA</InputAdornment>}}
                           disabled={isBusy}
                           error={mintAmountError}
                           helperText={mintAmountHelperText}
                />
                <Button
                    color="primary"
                    variant="contained"
                    size="large"
                    onClick={handleMintSubmit}
                    disabled={isBusy || mintAmountError}
                >
                    {isBusy ? <CircularProgress size={26}/> : "Mint"}
                </Button>
            </Box>
            <TxStatusToast successMessage="BOCA tokens minted successfully!"
                           state={mintTokensState}
                           resetState={mintTokensResetState}/>
        </>
    )
}
