import {useContractFunction, useEthers} from "@usedapp/core"
import {createBocaContract} from "../utils/createBocaContract";

export const useBurnTokens = () => {
    const {chainId} = useEthers()
    const bocaContract = createBocaContract(chainId)

    return useContractFunction(bocaContract, "burn", {
        transactionName: "Burn BOCA tokens",
    })
}
