import {ChainId} from "@usedapp/core";

export const isValidChain = (chainId: number | undefined) => {
    return chainId
        && chainId !== ChainId.Localhost // etherscan does not work for local chains
        && chainId !== ChainId.Hardhat
        && Object.values(ChainId).includes(chainId)
}

export const openInNewTab = (url: string) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
}

export const openAddressInEtherscan = (chainId: number | undefined, address: string | undefined): boolean => {
    if (!address || !isValidChain(chainId)) return false
    const network = ChainId[chainId!!].toLowerCase()
    const etherscanUrl = `https://${network}.etherscan.io/address/${address}`
    openInNewTab(etherscanUrl)
    return true
}

export const openTransactionInEtherscan = (chainId: number | undefined, hash: string | undefined): boolean => {
    if (!hash || !isValidChain(chainId)) return false
    const network = ChainId[chainId!!].toLowerCase()
    const etherscanUrl = `https://${network}.etherscan.io/tx/${hash}`
    openInNewTab(etherscanUrl)
    return true
}
