import {useCall, useContractFunction, useEthers} from "@usedapp/core"
import {createBocaContract} from "../utils/createBocaContract";
import {BigNumber} from "ethers";

export const useFees = () => {
    const {chainId} = useEthers()
    const bocaContract = createBocaContract(chainId)

    const {value: mintFeeValue} = useCall({
        contract: bocaContract,
        method: "getMintFee",
        args: [],
    }) ?? {}
    const mintFee: BigNumber | undefined = mintFeeValue ? mintFeeValue[0] : undefined;

    const {send: mintFeeSend, state: mintFeeState, events: mintFeeEvents, resetState: mintFeeResetState} = useContractFunction(bocaContract, "setMintFee", {
        transactionName: "Set mint fee",
    })

    const {value: burnFeeValue} = useCall({
        contract: bocaContract,
        method: "getBurnFee",
        args: [],
    }) ?? {}
    const burnFee: BigNumber | undefined = burnFeeValue ? burnFeeValue[0] : undefined

    const {send: burnFeeSend, state: burnFeeState, events: burnFeeEvents, resetState: burnFeeResetState} = useContractFunction(bocaContract, "setBurnFee", {
        transactionName: "Set burn fee",
    })

    const {value: feeDecimalsValue} = useCall({
        contract: bocaContract,
        method: "feeDecimals",
        args: [],
    }) ?? {}
    const feeDecimals: number | undefined = feeDecimalsValue ? feeDecimalsValue[0] : undefined;

    return {mintFee, mintFeeSend, mintFeeState, mintFeeEvents, mintFeeResetState, burnFee, burnFeeSend, burnFeeState, burnFeeEvents, burnFeeResetState, feeDecimals}
}
