import {Config, Localhost, Sepolia} from "@usedapp/core"
import {getDefaultProvider} from "ethers"

const providerOptions = {
    etherscan: process.env.REACT_APP_ETHERSCAN_API_KEY ?? "-",
    infura: process.env.REACT_APP_INFURA_API_KEY ?? "-",
    alchemy: process.env.REACT_APP_ALCHEMY_API_KEY ?? "-",
    pocket: process.env.REACT_APP_POCKET_API_KEY ?? "-",
    ankr: process.env.REACT_APP_ANKR_API_KEY ?? "-"
}

export const config: Config = {
    readOnlyChainId: Sepolia.chainId,
    readOnlyUrls: {
        [Sepolia.chainId]: getDefaultProvider('sepolia', providerOptions)
    },
    networks: [Sepolia],
    notifications: {
        expirationPeriod: 1000,
        checkInterval: 1000
    }
}

if(process.env.NODE_ENV==="development") {
    // started with "yarn start"
    config.readOnlyUrls!![Localhost.chainId]=Localhost.rpcUrl!!
    config.networks!!.push(Localhost)
}
